import Vue from 'vue'
import Router from 'vue-router'
import Demo from '@/components/Demo'
import Home from '@/components/Home'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Demo',
      component: Demo
    },
    {
      path: '/home',
      name: 'Home',
      component: Home
    }
  ]
})



